.animate-on-scroll {
  opacity: 0;
  transform: translateX(-50px);
  animation: none;
  will-change: opacity, transform;
}
.animate-on-scroll.in-view {
  opacity: 1;
  transform: translateX(0);
}
.animate-on-scroll.spin.in-view {
  animation: spin 6s linear infinite;
}
.animate-on-scroll.fadeInUp.in-view {
  animation: fadeInUp 0.8s ease forwards 0.12s;
}
.animate-on-scroll.pulse.in-view {
  animation: pulse 1.5s ease-out infinite;
}
.animate-on-scroll.fadeInUp.pulse.in-view {
  animation: fadeInUp 0.2s ease-out forwards, pulse 1.5s ease-out infinite;
  animation-delay: 0s, 0.5s;
}
.animate-on-scroll.shake.in-view {
  animation: gift-nudge 1.2s ease-out;
}
.animate-on-scroll.shake.shake-loop {
  animation: shake 0.6s infinite ease-in-out;
}
.animate-on-scroll.heartbeat.in-view {
  -webkit-animation: heartbeat 1.5s ease-in-out infinite both;
  animation: heartbeat 1.5s ease-in-out infinite both;
}

.running-line-right {
  animation: running-line-right 30s linear 1s infinite;
