.video {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 60px;
}
.video__block {
  position: relative;
  overflow: visible;
  width: 100%;
  height: auto;
  border-radius: var(--border-radius);
  transform-origin: center center;
  transform: scale(0.5);
  opacity: 0;
  transition: transform 0.8s ease-out, opacity 0.8s ease-out;
}
.video__block.is-visible {
  transform: scale(1);
  opacity: 1;
}
.video__block::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 5;
  background: var(--gray-900);
  opacity: 0.6;
  pointer-events: none;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.video__block::before--played::before {
  opacity: 0;
  visibility: hidden;
}
.video__player {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 2;
}
.video__button {
  position: absolute;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: var(--primary);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 6;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.video__button svg {
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.video__button::after {
  content: "";
  position: absolute;
  inset: -16px;
  border-radius: 50%;
  background-color: var(--primary);
  opacity: 0.12;
  transition: transform 0.4s ease, opacity 0.4s ease;
  z-index: 1;
  pointer-events: none;
}
.video__button:focus {
  outline: 2px solid var(--danger);
  outline-offset: 2px;
}
@media (any-hover: hover) {
  .video__button:hover {
    background-color: var(--danger);
  }
  .video__button:hover::after {
    transform: scale(1.2);
  }
  .video__button:hover svg {
    transform: scale(1.2);
  }
}
.video__button:active {
  background-color: var(--danger);
}
.video__button:active::after {
  transform: scale(1.4);
  opacity: 0.6;
}
.video__button:active svg {
  transform: scale(1.2);
}
.video__button--played {
  opacity: 0;
  visibility: hidden;
}
.video__muted {
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.12);
  left: 52px;
  bottom: 12px;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.video__muted:focus {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}
@media (any-hover: hover) {
  .video__muted:hover {
    background-color: rgba(255, 255, 255, 0.4);
  }
}
.video__muted:active {
  background-color: rgba(255, 255, 255, 0.4);
}

