  font-size: 16px;
  line-height: 1.6;
}
.post-form__btn {
  width: 215px;
  margin-top: 48px;
  margin-right: auto;
  margin-left: auto;
}
.post-form textarea {
  resize: none;
  height: 100px;
}
@media screen and (max-width: 485.01px) {
  .post-form__inner {
    flex-direction: column;
  }
  .post-form__label {
    width: 100%;
  }
}

.contacts {
  padding-top: clamp(2.5rem, 0.742rem + 8.79vw, 7.5rem);
  padding-bottom: clamp(2.5rem, 0.742rem + 8.79vw, 7.5rem);
}
.contacts__title {
  margin-bottom: 24px;
}
.contacts__inner {
  display: flex;
  justify-content: space-between;
  gap: 30px 10px;
}
@media screen and (max-width: 1025.01px) {
  .contacts__inner {
    flex-direction: column;
    align-items: center;
  }
}

.contact-form__grup {
  display: flex;
  justify-content: space-between;
  gap: 30px;
}
.contact-form .form__label {
  font-size: 16px;
  line-height: 1.6;
  width: 285px;
  margin-bottom: 24px;
}
.contact-form .form__label--big {
  width: 600px;
}
.contact-form .form__input {
  margin-top: 8px;
  padding: 13px 16px;
  width: 100%;
  font-size: 16px;
  line-height: 1.6;
  height: 52px;
}
.contact-form .form__textarea {
  height: 128px;
}
.contact-form__policy-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  margin-top: 40px;
}
.contact-form__policy-box .checkbox__text {
  max-width: 315px;
}
.contact-form .button--large {
  padding: 0 42px;
}
@media screen and (max-width: 1025.01px) {
  .contact-form .form__label--big {
    width: 100%;
  }
}
@media screen and (max-width: 768.01px) {
  .contact-form__grup {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
  .contact-form__policy-box {
    flex-direction: column;
  }
}

.choices {
  width: 100%;
  margin-top: 8px;
}
.choices__inner {
  height: 52px;
  padding: 13px 16px !important;
  font-size: 16px;
  line-height: 1.6;
  color: var(--gray-800);
  border: 1px solid var(--gray-400);
  border-radius: var(--border-radius);
  color: var(--gray-600);
  background-color: var(--gray-200);
  width: 100%;
}
.choices__list--single {
  padding: 0px 16px 4px 2px;
}
.choices__list--single .choices__placeholder {
  color: var(--gray-800);
  opacity: 1;
}
.choices[data-type*=select-one]:after {
  width: 14px;
  height: 9px;
  border: none;
  background: url(../images/icons/down.svg) center/cover no-repeat;
  right: 16px;
  margin-top: 0 !important;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.choices[data-type*=select-one].is-open:after {
  transform: rotate(180deg);
}
.choices__list--dropdown .choices__item--selectable::after {
  display: none;
}
.choices.is-focused {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

.custom-radio__input {
  position: absolute;
  opacity: 0;
}
.custom-radio__text {
  position: relative;
  display: inline-block;
  padding-left: 27px;
  font-size: 16px;
  line-height: 160%;
}
.custom-radio__text::before {
  content: "";
  position: absolute;
  left: -1px;
  top: 4px;
  display: inline-block;
  border: 1px solid var(--gray-500);
  border-radius: 100%;
  width: 16px;
  height: 16px;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.custom-radio__text::after {
  content: "";
  position: absolute;
  left: -1px;
  top: 4px;
  width: 16px;
  height: 16px;
  background-image: url("../images/icons/check-indicator.svg");
  background-position: center;
  background-size: 8px 8px;
  background-repeat: no-repeat;
  opacity: 0;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.custom-radio__input:checked + .custom-radio__text::before {
  border-color: var(--primary);
}
.custom-radio__input:checked + .custom-radio__text::after {
  opacity: 1;
}
.custom-radio__input:focus + .custom-radio__text {
  outline: 2px solid var(--primary);
}

.offices {
  padding-top: clamp(2.5rem, 1.621rem + 4.4vw, 5rem);
  padding-bottom: clamp(2.5rem, 0.698rem + 9.01vw, 7.625rem);
  background: var(--gray-200);
}
.offices__nav-title {
  font-weight: 700;
  font-size: clamp(1.125rem, 0.086rem + 2.16vw, 1.75rem);
  line-height: 1.5;
  color: var(--gray-900);
  margin-bottom: 24px;
}
.offices__nav {
  display: grid;
  --valeus-gap: clamp(0.125rem, -12.135rem + 25.54vw, 7.5rem);
  gap: calc(var(--valeus-gap) * 2);
  grid-template-columns: repeat(3, 1fr);
}
.offices__nav-coll {
  width: 252px;
  position: relative;
  grid-column: 1 span;
}
.offices__nav-coll:not(:last-child)::after {
  position: absolute;
  content: "";
  top: 0;
  right: calc(-1 * var(--valeus-gap));
  width: 1px;
  height: 100%;
  background-image: url(../images/icons/divider-offices.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 5;
}
.offices__item {
  width: 100%;
  margin-bottom: 4px;
}
.offices__item--address {
  margin-bottom: 20px;
}
.offices__item:last-child {
  margin-bottom: 0;
}
.offices__address {
  display: block;
  margin-bottom: 4px;
}
.offices__link {
  font-size: 16px;
  line-height: 1.6;
  color: var(--gray-900);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.offices__link:focus {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}
@media (any-hover: hover) {
  .offices__link:hover {
    color: var(--primary);
  }
}
.offices__link:active {
  color: var(--primary);
}
.offices__link--map {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: var(--primary);
}
.offices__link--name {
  color: var(--gray-700);
}
.offices__social {
  padding-top: 120px;
}
.offices__social-title {
  margin-bottom: 40px;
}
.offices__social .social {
  justify-content: center;
  gap: 24px;
}
@media screen and (max-width: 1025.01px) {
  .offices__nav-coll:not(:last-child)::after {
    display: none;
  }
}
@media screen and (max-width: 768.01px) {
  .offices__nav-coll {
    grid-column: 12 span;
    width: 100%;
    text-align: center;
  }
  .offices__nav-coll:not(:last-child) {
    margin-bottom: 30px;
  }
}