.fade-scale {
  animation-name: fadeScale;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
  animation-duration: 1s;

  view-timeline-name: --fadeScale-block;
  animation-timeline: --fadeScale-block;
  animation-range: 20% 50%;
}

.fadeInUp {
  animation: fadeInUp 0.8s ease forwards 0.12s;
}

// Анимация появления элементов
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeIn {
  from {
    scale: 0.5;

  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes shake {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translate(-1.5px, 1.5px) rotate(-1.5deg);
  }

  40% {
    transform: translate(-1.5px, -1.5px) rotate(1.5deg);
  }

  60% {
    transform: translate(1.5px, 1.5px) rotate(0.5deg);
  }

  80% {
    transform: translate(1.5px, -1.5px) rotate(1deg);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes fadeScale {
  0% {
    scale: 0.5;
    // opacity: 0;
  }

  // 40% {
  //   opacity: 0.4;
  // }

  100% {
    scale: 1;
    // opacity: 1;
  }
}

@keyframes to-right {
  0% {
    transform: translate3d(-10%, 0, 1px);
  }

  100% {
    transform: translateZ(1px);
  }
}

