// 🎯 Миксин для анимации раскрытия фона на page-hero
@mixin my-reveal-bg($duration: 1s,
  $timing: ease-out,
  $start-size: 0% 100%,
  $anim-name: my-revealBg,
  $end-size: null) {
  // Автоматически получаем end-size из карты, если не задано вручную
  $resolved-end-size: if($end-size ==null and map-has-key($reveal-bg-sizes, str-replace($anim-name, "my-revealBg-", "")),
      map-get($reveal-bg-sizes, str-replace($anim-name, "my-revealBg-", "")),
      $end-size );

  background-size: $start-size;
  animation: $anim-name $duration $timing forwards;

  // fallback: если resolved-end-size не null, можно использовать его где-то ещё
}

@mixin generate-reveal-keyframe($anim-name,
  $end-size: 50% 100%) {
  @keyframes #{$anim-name} {
    to {
      background-size: $end-size;
    }
  }
}

// Карта размеров анимации
$reveal-bg-sizes: (
  "100-100": 100% 100%,
  "50-100": 50% 100%,
  "56-100": 56% 100%,
  "52-100": 52% 100%,
  "46-100": 46% 100%,
  "52-85": 52% 85%,
  "cover": cover
);

@each $name, $size in $reveal-bg-sizes {
  @include generate-reveal-keyframe(my-revealBg-#{$name}, $size);
}


/*

// // 📦 Использование:


Сообщение для Copilot
📌 Зачем нужен $anim-name Переменная $anim-name позволяет: ✅ Назвать @keyframes анимацию динамически;

✅ Использовать одно и то же @mixin reveal-bg(...) с разными названиями;

✅ Избежать конфликта,
если ты вызываешь миксин несколько раз с разными параметрами (например, на .page-hero и .banner).
🧠 Как это работает Вот ключевой момент:



миксин вставит имя из параметра — например,
revealBg,
bgExpandHero,
bgFadeSlide,
и т.д.
✨ Пример использования scss .page-hero {
  @include reveal-bg($duration: 1.2s,
    $start-size: 0% 100%,
    $end-size: 100% 100%,
    $anim-name: bgExpandHero);
}

Это создаст: css @keyframes bgExpandHero {
  ...
}

И применит animation: bgExpandHero 1.2s ease-out forwards;
— красиво и изолированно 💅
*/


@keyframes expandLeft {
  from {
    transform: scaleX(0);
    transform-origin: left;
  }

  to {
    transform: scaleX(1);
    transform-origin: left;
  }
}

@keyframes expandRight {
  from {
    transform: scaleX(0);
    transform-origin: right;
  }

  to {
    transform: scaleX(1);
    transform-origin: right;
  }
}

.img-expand-left {
  animation: expandLeft 1s cubic-bezier(0.4, 0, 0.6, 1) forwards;
}

.img-expand-right {
  animation: expandRight 1s cubic-bezier(0.4, 0, 0.6, 1) both;
}

@mixin expand-img($origin: left, $duration: 1s) {
  transform: scaleX(0);
  transform-origin: $origin;
  animation: expand#{$origin} $duration cubic-bezier(0.4, 0, 0.6, 1) forwards;
}


@keyframes clip-path {
  from {
    opacity: 0;
    clip-path: inset(45% 20% 45% 20%);
  }

  to {
    opacity: 1;
    clip-path: inset(0% 0% 0% 0%);
  }
}

.revealing-image {
  /* Create View Timeline */
  view-timeline-name: --revealing-image;
  view-timeline-axis: block;

  /* Attach animation, linked to the  View Timeline */
  animation: linear clip-path both;
  animation-timeline: --revealing-image;

  /* Tweak range when effect should run*/
  animation-range: entry 15% cover 50%;
}


@keyframes scaleUp {
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@mixin appear-scale($duration: 0.8s, $easing: ease-out, $origin: center center, $scale: 0.5) {
  transform-origin: $origin;
  transform: scale($scale);
  opacity: 0;
  transition: transform $duration $easing, opacity $duration $easing;

  &.is-visible {
    transform: scale(1);
    opacity: 1;
  }
}

@mixin appear-scale3d($duration: 0.8s,
  $easing: ease-out,
  $origin: center center) {
  transform-origin: $origin;
  animation: scale3d $duration $easing forwards;
}

@keyframes scale3d {

  0% {
    transform: scale3d(0, 0, 0);
    opacity: 0;
  }

  100% {
    transform: scale3d(1, 1, 1);
    opacity: 1;
  }
}